import startMask from "../masked_text_field";
import "../buttons";
import "../radio_button";
import "../toggle";
import "../text_field";

import "./style.scss";

export default function registerInit() {
  startMask("js-register-from-birthday");

  const formEl = document.querySelector(".js-register-form__form");

  const submitFormHandler = (e) => {
    e.stopPropagation();
    e.preventDefault();

    /* eslint-disable no-console */
    console.log("register form submit");
  };

  formEl.addEventListener("submit", submitFormHandler);
}
